.slider {
    position: relative;
    width: 100%;
    height: 100vh; /* chiu cao full mn hnh */
    overflow: hidden;
  }
  
  .slide {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; /* Thit lp chiu cao c nh cho tt c cc slide */
  }
  
  .slide img {
    width: 100%;
    height: 100%;
    object-fit: cover; /*  nh chim ton b kch thc ca slide */
  }

  .slide:nth-child(3) img {
    /* t kch thc c nh cho nh th ba */
    height: 90%; /* hoc bt k gi tr no khc ph hp */
  }
  